@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body, h1, h2, ul, p, h4 {
	margin-block-start: 0;
    margin-block-end: 0;
	margin: 0;
	padding: 0;
	font-family: 'Mulish', sans-serif;
}

ul {
	list-style: none;
}

.homepage {
	display: flex;
}

.container {
	padding: 20px;
		&-sorts {
			width: 150px;
			background-color: rgb(213, 213, 213);
			height: 1;
				h2 {
					font-weight: 400;
					font-size: 22px;
					margin-bottom: 15px;
				}
		}
		&-users {
			display: flex;
			flex-direction: column;
				h1 {
					margin-bottom: 15px;
				}

				ul {
					margin-left: 30px;
				}
		}
		&-card {
			h1 {
				margin-bottom: 15px;
			}
		}
		&-label {
			margin-left: 30px;
		}
		&-header{
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 60vw;
		}
		&-button {
			
			button {
				cursor: pointer;
				margin-right: 10px;
				padding: 7px 15px;
				border-radius: 4px;
				border: none;

				&.send {
					color: #fff;
					background: rgb(74, 213, 61);
					&:hover {
						background: rgb(62, 203, 1);
					}
				}
				&.edit {
					color: #fff;

					background: rgb(71, 58, 255);
					&:hover {
						background: rgb(59, 48, 205);
					}
				}

			}
		}
}

.sortsBy {
	.sort {		
		cursor: pointer;
		font-size: 20px;
		color: #fff;
		background-color: rgb(71, 58, 255);
		border: 1px solid rgb(71, 58, 255);
		padding: 3px;
		border-radius: 4px;
		text-align: center;
		width: 8rem;
		transition: all 0.2s ease-in-out;
		margin-bottom: 10px;

		&:hover {
			background-color: rgb(59, 48, 205);
			border: 1px solid rgb(59, 48, 205);
			}
		&.active {
			transform: translate(8px, 0);
		}
	}
}

.disabled {
	font-size: 20px;
	color: #fff;
	background-color: rgb(73, 73, 73);
	border: 1px solid rgb(73, 73, 73);
	padding: 3px;
	border-radius: 4px;
	text-align: center;
	width: 8rem;
	margin-bottom: 10px;
}

.user {
	display: flex;
	justify-content: space-between;
	align-items: end;
	padding: 15px;
	margin-bottom: 15px;
	width: 60vw;
	background-color: rgb(241, 241, 241);
  	border-radius: 10px;
	  .info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 70px;
			div {
				display: flex;
				h4 {
					font-weight: 100;
					color: rgb(112, 112, 112);
					margin-right: 7px;
				}
			}
	  }
}

.label {
	display: flex;
	flex-direction: column;
	margin-bottom: 12px;

	label{
		margin-bottom: 6px;
	}

	input{
		height: 20px;
		width: 300px;
	}

	&:last-child:not(:only-child) {
		height: 50px;
		width: 800px;
	}
}

